import * as React from "react";
import { Link } from 'react-router-dom'


import fail from '../../assets/img/sign/fail.png'
import bind from '../../assets/img/sign/bind.png'
import failog from '../../assets/img/sign/failog.png'
import erwei from '../../assets/img/sign/erwei.jpg'
import cuo from '../../assets/img/sign/cuo.png'
import fail_head from '../../assets/img/sign/fail-head.png'
import './signcom.less'

interface ISprop {
    [propsName: string]: any
}
interface IState {
}

// 2001, "",
//     2002, ""),
//    2003, ""),
//     2004, ""),
export default class SignFail extends React.Component<ISprop, IState>{
    constructor(props: any) {
        super(props);
        this.state = {
        }
    }
    componentDidMount() {
    }
    checkStatus = (status) => {
        switch(status) {
            case 2001:
            return (
                <div className="normal">
                    <p className="title2"> <br/></p>
                    <img src={erwei} className="erwei" alt=""/>
                    <p className="bott"></p>
                </div>
            )

            case 2002:
            return (
                <div className="normal">
                    <p className="title2"></p>
                    <img src={bind} className="phone" alt=""/>
                    <Link className="bind" to="/bind/bindPhone"></Link>
                </div>
            )

            case 2003:
            return (
                <div className="normal">
                    <p className="title"> </p>
                    <div className="reason">
                        <p></p>
                        <p className="tips tips1">
                            <span>1</span>
                            <br/>
                            <Link to="/bind/bindPhone"></Link>
                        </p>
                        <p className="tips tips2">
                            <span>2</span>
                            
                        </p>
                        <p className="tips tips3">
                            <span>3</span>
                            
                        </p>
                    </div>

                    <Link to="" className="apply"></Link>
                </div>
            )

            case 2004:
            return (
                <div className="normal">
                    <p className="title2"></p>
                    <img src={failog} className="failog" alt=""/>
                </div>
            )

            default:
            return (
                <div className="normal">
                    <p className="title"> </p>
                    <div className="reason">
                        <p></p>
                        <p className="tips tips1">
                            <span>1</span>
                            <br/>
                            <Link to="/bind/bindPhone"></Link>
                        </p>
                        <p className="tips tips2">
                            <span>2</span>
                            
                        </p>
                        <p className="tips tips3">
                            <span>3</span>
                            
                        </p>
                    </div>
                    <Link to={`/normalMember?courseId=${this.props.courseId}`} className="apply"></Link>
                </div>
            )
        }
    }
    render() {
        const {result} = this.props;
        return (
            <div className="fail-con">
                <div className="fail-detail">
                    <img className="con-logo" src={fail_head} alt="" />
                    <h2>
                        <img src={cuo} alt=""/>
                    </h2>
                    {
                        this.checkStatus(result.status)
                    }
                </div>
            </div>

        )
    }

}
